/**
 * Service file to handle API requests related to coordinators and bookings.
 */

const BASE_URL = process.env.NEXT_PUBLIC_API_URL as string; // Base API URL from environment variables

// Type definitions
export interface Coordinator {
  _id: string;
  name: string;
  location: string;
  price: number;
  profilePhoto: string;
  bio: string;
  rating: number;
  reviews: number;
  experience: string;
  specialty: string;
  languages: string[];
  unavailableDates: string[];
}

export interface BookingRequest {
  coordinatorId: string;
  name: string;
  email: string;
  weddingDate: string;
  guestNumber: number;
}

/**
 * Fetch all coordinators from the backend API.
 * @returns Promise resolving to an array of Coordinator objects.
 */
export const getCoordinators = async (): Promise<Coordinator[]> => {
  const res = await fetch(`${BASE_URL}/coordinators`);
  if (!res.ok) throw new Error("Failed to fetch coordinators");
  return res.json();
};

/**
 * Fetch a single coordinator by ID.
 * @param id - The ID of the coordinator.
 * @returns Promise resolving to a single Coordinator object.
 */
export const getCoordinator = async (id: string): Promise<Coordinator> => {
  const res = await fetch(`${BASE_URL}/coordinators/${id}`);
  if (!res.ok) throw new Error("Failed to fetch coordinator");
  return res.json();
};

/**
 * Create a new booking for a coordinator.
 * @param data - Booking form data containing user details and wedding information.
 * @returns Promise resolving to the booking response.
 */
export const createBooking = async (data: BookingRequest): Promise<any> => {
  const res = await fetch(`${BASE_URL}/bookings`, {
    method: "POST",
    headers: {"Content-Type": "application/json"},
    body: JSON.stringify(data),
  });
  if (!res.ok) throw new Error("Failed to create booking");
  return res.json();
};
