"use client";

import {useEffect, useState} from "react";
import {useTheme} from "@/hooks/useTheme";
import Navbar from "@/components/Navbar";
import CoordinatorCard from "@/components/CoordinatorCard";
import {Coordinator, getCoordinators} from "@/services/coordinatorService";

export default function ClientHome() {
  const {theme, toggleTheme} = useTheme();
  const [coordinators, setCoordinators] = useState<Coordinator[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);

  // Fetch coordinators on component mount
  useEffect(() => {
    const fetchCoordinators = async () => {
      setLoading(true);
      setError(null);
      try {
        const data = await getCoordinators();
        setCoordinators(data);
      } catch (err) {
        console.error("Error fetching coordinators:", err);
        setError("Failed to load coordinators.");
      } finally {
        setLoading(false);
      }
    };

    fetchCoordinators();
  }, []);
  

  return (
    <div className={`antialiased transition-colors duration-300`}>
      <Navbar theme={theme} toggleTheme={toggleTheme} />

      {/* Coordinators Section */}
   
        <h1 className='text-3xl font-bold mb-6 text-center'>
          Our Wedding Coordinators
        </h1>
        <div className='grid grid-cols-1 md:grid-cols-3 gap-6'>
          {loading && <p>Loading coordinators...</p>}
          {error && <p className='text-red-500'>{error}</p>}
          {!loading && !error && (
            <div className='grid grid-cols-1 md:grid-cols-3 gap-6'>
              {coordinators.map((coordinator) => (
                <CoordinatorCard
                  key={coordinator._id}
                  coordinator={coordinator}
                />
              ))}
            </div>
          )}
        </div>
    

      {/* Optional Components */}
      {/* <Hero /> */}
      {/* <Projects /> */}
      {/* <About /> */}
      {/* <Skills /> */}
      {/* <Contact /> */}
      {/* <Footer /> */}
    </div>
  );
}
